@import url(https://fonts.googleapis.com/css2?family=Didact+Gothic&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,300;1,600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Didact+Gothic&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,
body{
  height: 100%;
}
.wrapper{
  width: 100%;  
  min-height: 100vh;  
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;  
  position: relative; 
}
.bg-image{
 width: 100%;  
  min-height: 100vh;
  background-image: url(/hw_react_04/static/media/bg.813b97af.jpg);
  background-size: cover;    
}
.insertImg{
  width: 250px;
  object-fit: cover;
  position: fixed;
  right: 50px;
  bottom: 50px;
  border-radius: 50%;  
}
.nav{
  width: 100%;
  height: 60px;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;  
  background-color:   #dbdbf0;
  z-index: 3; 
}
.app-link{
  color: #fff;
  text-decoration: none;
  font-size: 18px;
  margin-right: 20px;
  margin-left: 200px;
  font-weight: 500;
  text-shadow: 1px 1px #4d0066;
  z-index: 5;
}
.app-link:hover{
  text-decoration: underline;
}
.active-link{
  color: #9966ff;
}
.switch{
  display: flex;
  width: 100%;
  justify-content: center;
  z-index: 5;
}


.main{
	width: 100%;
	height: 100vh;
	/*display: flex;
	justify-content: center;
	align-items: flex-start;
	padding-top: 250px;*/
	position: relative;
}

.mainTitle{
	position: absolute;
	top: 30%;
	left: 40%;
	transform: translate(-30%, -40%);	
	font-family: 'Didact Gothic', sans-serif;
	font-size: 60px;
	color: #9966ff;
	z-index: 10;
}

.insertImg{
  width: 250px;
  object-fit: cover;
  position: fixed;
  right: 50px;
  bottom: 50px;
  border-radius: 50%;  
}
.Post{
	margin: 0 auto;
	margin-top: 100px;
	padding: 20px;
	width: 500px;	
	background-color: #8585ad;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}
.head{
	display: flex;
	width: 100%;
	align-items: center;
	justify-content: flex-start;
}
.head__avatar{
	display: block;	
	width: 50px;
	height: 50px;
	border: .1px solid #8585ad;
	border-radius: 50%;
	object-fit: cover;
}
.head__inf{
	display: flex;
	justify-content: flex-start;
	align-items: center;
	padding-left: 10px;
}
.head__inf li{
	margin-right: 10px;
	list-style-type: none;
}
.head__name{
	font-size: 16px;
	font-weight: 700;
	color: #fff;
}
.head__nickName,
.head__date{
	font-size: 14px;
	color: #c6d9ec
}
.content{
	display: flex;
	width: 100%;
	flex-direction: column;
	width: 90%;
	align-self: flex-end;
}
.content__text{
	display: inline-block;
	text-align: left;
	font-size: 20px;
	font-weight: 500;
	color: #fff;
	padding-left: 15px;
}
.content__photo{
	width: 100%;
	border-radius: 20px;
}


.contracts-block{
	width: 300px;
	margin: 0 auto;
	margin-top: 120px;
}
.input{
	width: 280px;
	padding: 10px;
	font-size: 12px;
	font-weight: 300;
	margin-bottom: 15px;
}
.contact {	
	display: flex;
	justify-content: flex-start;
	align-items: center;
	background-color: #cc99ff;
	margin-top: 5px;
	padding: 10px;
}
.contact__gender{
	display: block;
	width: 30px;
	height: 30px;
	object-fit: cover;
}
.contact__data{
	display: flex;
	flex-direction: column;
	justify-content: center;	
	margin-left: 30px;
	font-family: 'Nunito', sans-serif;
	font-style: italic;
	text-align: left;
	color: #4d4d00;
}
.contact__fullName{
	display: inline-block;
	font-size: 18px;
	font-weight: 600;
	margin: 0;	
}
.contact__phone{
	display: inline-block;
	font-size: 13px;
	font-weight: 300;
	margin: 0;
}
.checkboxArea{
	display: flex;
	justify-content: space-between;
}
.checkboxArea p{
	font-size: 14px;
	color: #fff;
}
.contactLink{
	text-decoration: none;	
}
.card{
	width: 30%;
	margin-top: 250px;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	font-size: 30px;
	color: #fff;
	background-color: rgba(204, 153, 255, .5);
}
.container{
	width: 90%;
	margin-top: 30px;
	display: flex;
	flex-direction: column;	
}

h2{
	font-family: 'Didact Gothic', sans-serif;
	font-size: 60px;
	color: #9966ff;
}

.flex,
.flex-square{
	width: 100%;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	margin-top: 50px;
}
.flex-square{
	width: 80%;
	margin: 0 auto;
	margin-top: 50px;	
	justify-content: space-around;
}
.picture{
	width: 500px;	
}
.img{
	width: 100%;
	object-fit: cover;
}
